* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
body {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 16px;
	background: #f6f6f6;
	overflow: auto;
}


h1 {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 26px;
	font-weight: bold;
	color: #076cbe;
}
h4 {
	font-family:  Arial, Helvetica, sans-serif;
	font-size: 20px;
	font-weight: bold;
	color: #3a3939;
}
p {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 18px;
	color: #4b4d4c;
}
a {
	text-decoration: none;
}

ul {
	list-style: none;
}

img {
	max-width: 100%;
}

.container {
	width: 100%;
	margin: 0 auto;
	padding: 0 0px;
}



.container .title {
	color: #6b6c6c;
	font-size: 30px;
	font-weight: 600;
	margin: 40px 0;
}
/*Menu Navi*/
nav {
	padding: 0 20px;
	width: 100%;
	height: 90px;
	z-index:99;
}

.logo h1 {
	color: #0097e6;
	
}

nav .nav-items {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	height: 100%;
	-ms-flex-align: center;
	    align-items: center;
}

.nav-items ul {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.nav-items ul li {
	list-style: none;
	margin-left: 20px;
	font-size: 18px;
	font-weight: 600;
	color: #000;
}

.nav-items ul li a {
	color: #000;
}

.nav-items ul li a:hover {
	color: #00a8ff;
}

.nav-items ul li a.active {
	color: #00a8ff;
}






/* error component styling */

.error-page {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin-top: 20px;
	height: 60vh;
}

.error-page .error-code {
	font-size: 150px;
	font-weight: 600;
	color: #00a8ff;
}

.error-page .error-text {
	font-size: 34px;
	font-weight: 600;
	color: #000;
}

/***************************/


/* Grid settings */
.row::after {
  content: "";
  clear: both;
  display: block;
}



/*page_footer ohne margin-bottom*/
[class*="col-"]{
	float: left;
	margin-bottom: 5vh;
}

/* For desktop: */
.col-1 {width: 8.33%;}
.col-2 {width: 16.66%;}
.col-3 {width: 25%;}
.col-4 {width: 33.33%;}
.col-5 {width: 41.66%;}
.col-6 {width: 50%;}
.col-7 {width: 58.33%;}
.col-8 {width: 66.66%;}
.col-9 {width: 75%;}
.col-10 {width: 83.33%;}
.col-11 {width: 91.66%;}
.col-12 {width: 100%;}


@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  [class*="col-"] {
        width: 100%;
  }
  button {
      min-width: 32vw; 
  }
}



.box_1{
  background-color: gray;
}
.box_2{
  background-color: red;
}
.box_3{
  background-color: rgb(32, 11, 124);
}
.box_4{
  background-color: rgb(13, 192, 52);
}


.header {
  width:100%;
  height:100vh;
  font-size:12px;
  font-family:Arial;
  overflow-x: hidden;			
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header_text_content{
  position:absolute; 
  left:5%; 
  top: 65%; 
  color:#ffffff; 
  height:50vh;
}
.header_text_content h1{
	font-Size:5vh;

  }
button {
  font-Size:1.8vh;
  top: 69%;
  left:6%;
  right: 0%;
  min-width: 8vw; 
  height: 11px;
  text-decoration: none;
  display: -ms-inline-flexbox;
  display: inline-flex;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-height: 44px;
  border: 2px solid;
  border-radius: 22px;
  outline: currentcolor none medium;
  cursor:pointer;
  background-color: #00295c;
  color:#ffffff;
  margin-top: 10px;
}

.button:hover{
      background-color: #0068aa;
      color:#111111;
}

.mini_btn {
	font-family: Arial, Helvetica, sans-serif;
	background: #05baf7e4;
	color: #fff;
	min-width: 5vw; 
	font-size: 12px;
	cursor: pointer;
}

.mini_btn:hover {
	background: #8c8e8f;
}



img {
    display: block;
    height: auto;
    max-width: 100%;
}



/*Formular*/
form input[type="text"],
form input[type="email"],
form input[type="search"],
form textarea { 
  	width: 50%; 
	min-width: 20%; 
	height: 40px;
  	-webkit-box-sizing: border-box;
  	        box-sizing: border-box;
  	padding: 4px;
  	outline: none;
  	border-radius:6px; 
  	border:2px solid silver;

}


/*footer*/
.page_footer {
    background-color: #d8dbdb;
    opacity: 0.5;
    text-align: center;
   
	
}
.page_footer h4 {
	margin-top: 2vh;
	color: #363535;
	line-height: 2;
	font-family:Arial, Helvetica, sans-serif;
	font-size: 22px;
	margin-bottom: 2vh;

}
.page_footer p {
	color: #0e0d0d;
	line-height: 1.6;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 12px;
	margin-bottom: 2vh;
}







